/**
 * Layout component that queries for data
 * with Gatsby's useStaticQuery component
 *
 * See: https://www.gatsbyjs.org/docs/use-static-query/
 */

import React from "react"
import PropTypes from "prop-types"
import { useStaticQuery, graphql } from "gatsby"
import SVG from 'react-inlinesvg';
import facebook from "../images/facebook.svg"
import twitter from "../images/twitter.svg"
import insta from "../images/insta.svg"

import Header from "./header"
import "./layout.css"

const Layout = ({ children }) => {
  const data = useStaticQuery(graphql`
    query SiteTitleQuery {
      site {
        siteMetadata {
          title
        }
      }
    }
  `)

  return (
    <>
      <div class="site">
        <Header siteTitle={data.site.siteMetadata.title} />
        <div class="site-content">
          <main>{children}</main>
        </div>
        <footer class="footer">
          <div class="footer-wrapper">
            <p class="footer-left">
              This TEDx event is operated under license from TED.
              <div>
                <a href="https://www.facebook.com/tedxjhu/" target="_blank" rel="noopener noreferrer">
                    <SVG id="icon" src={facebook}> <img src={facebook} alt="git" /> </SVG>
                </a>
                <a href="https://www.instagram.com/tedxhopkins/" target="_blank" rel="noopener noreferrer">
                    <SVG id="icon" src={insta}> <img src={insta} alt="linkedin" /> </SVG>
                </a>
                <a href="https://twitter.com/tedxjhu?lang=en" target="_blank" rel="noopener noreferrer">
                    <SVG id="icon" src={twitter}> <img src={twitter} alt="email"/> </SVG>
                </a>
              </div>
            </p>
            
            <p class="footer-right">Built by Arpan Sahoo</p>
          </div>
        </footer>
      </div>
    </>
  )
}

Layout.propTypes = {
  children: PropTypes.node.isRequired,
}

export default Layout
